import {
  Component,
  OnInit,
  SimpleChanges,
  Input,
  EventEmitter,
  Output
} from "@angular/core";
import { Customer } from "../../../../entities/customer.entity";
import { InvoiceFormGroup } from "../../../../forms/invoice.form";
import { RestService } from "./../../../../common/rest.service";
import { Sales } from "app/entities/sales.entity";
import { AppService } from "app/shared/service/app.service";
import { Storage } from "../../../../shared/utils/storage";
import { MdDialog } from "@angular/material";
import { Item } from "app/entities/item.entity";
import { Conditional } from "@angular/compiler";

@Component({
  selector: "app-customer-goods-return-search",
  templateUrl: "./customer-goods-return.component.html",
  styleUrls: ["./customer-goods-return.component.scss"]
})
export class CustomerGoodsReturnComponent implements OnInit {
  @Input() sales: Sales;
  //customerMobile: string = "";

  // @Input() selectedItem: Sales;
  customerMobile: any;
  invoiceNo: any;
  customerGst: any;
  customerName: any;
  invoiceDate: any;
  returnType: any[] = [];
  customerType = ["Return", "Exchange"];
  @Input()
  customers: Customer[] = [];
  @Input()
  filteredPhones: any[] = [];
  @Output()
  outputEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output()
  invoiceEmitter: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  salesInvoice: Sales[] = [];
  stateList: any[] = [];

  @Input()
  inputData: Sales;
  constructor(
    private appService: AppService,
    private dialog: MdDialog,
    private restService: RestService
  ) {}

  ngOnChanges(changes: SimpleChanges) {
    //this.temp = [ ...this.dataRows ];
    // this.sales = changes['sales'].currentValue
    // console.log(this.sales)
  }

  ngOnInit() {
    let isEditable = this.appService.getParam("isEditable");
    if (isEditable != undefined) {
      this.invoiceNo = this.sales.invoiceNumber;
      this.customerGst = this.sales.customer.gstNo;
      this.customerName = this.sales.customer.firstName;
    } else {
      console.log("no selectedItem available");
    }
    this.stateLoad();
  }
  changeProduct(item: any) {
    // this.outputProductToggle.emit(item);
    this.sales;
  }

  // filterPhones(val: string) {
  //   this.filteredPhones =
  //     val && val != ""
  //       ? this.customers.filter(
  //           s =>
  //             s.businessName.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
  //             s.mobile.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
  //             s.gstNo.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
  //             s.firstName.toLowerCase().indexOf(val.toLowerCase()) >= 0
  //         )
  //       : this.customers;
  // }

  filterPhones(val: string) {
    this.filteredPhones =
      val && val != ""
        ? this.salesInvoice.filter(
            s => s.invoiceNumber.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.customers;
  }

  stockItemList: Item[] = [];
  selectedInvoice(item: Sales) {
    console.log(item);
    this.stockItemList = item.items.filter(item => {
      item.stockItemId != null;
    });

    console.log(
      "stock Item list for the selected invoice ",
      item.invoiceNumber,
      this.stockItemList
    );

    if (item && typeof item == "object") {
      this.sales = item;
      this.customerGst = item.customer.gstNo;
      this.customerName = item.customer.firstName;
      this.invoiceDate = item.invoiceDate;
      // this.invoiceEmitter.emit(this.sales);
      // console.log(this.invoiceEmitter.emit(this.sales));
      this.onSelectCustomer(item.customer);
    } else {
      this.inputData.customer = new Customer();
      this.customerMobile = item;
      this.customerGst = item.customer.gstNo;
      this.invoiceDate = item.invoiceDate;
      this.customerName = item.customer.firstName;
      // this.invoiceEmitter.emit(item);
      // console.log(this.invoiceEmitter.emit(this.sales));
    }
  }

  onSelectCustomer(customer: Customer) {
    var companyInfo = Storage.getSessionUser().company;
    console.log(companyInfo);
    if (companyInfo.address.state == customer.shippingAddress.state) {
      this.sales.isCgst = true;
      this.sales.isIgst = false;
    } else {
      this.sales.isCgst = false;
      this.sales.isIgst = true;
    }
  }

  stateLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.stateList = results["stateList"];
      });
  }
  filteredStates: any[] = [];
  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.key.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }
}



