  changeProductService(flipProduct: boolean) {
    setTimeout(() => {
      if (!this.sales.isProduct) {
        this.productSubTotal = 0;
        this.sales.productItems = [];
        this.changeGrandCalculate();
      }
      if (!this.sales.isService) {
        this.serviceSubTotal = 0;
        this.sales.serviceItems = [];
        this.changeGrandCalculate();
      }

      console.log(this.sales.productItems);
      if (
        this.sales.isProduct &&
        this.sales.productItems != null &&
        this.sales.productItems.length == 0
      ) {
        this.addProduct();
      }
      if (
        this.sales.isService &&
        this.sales.serviceItems != null &&
        this.sales.serviceItems.length == 0
      ) {
        console.log("isService is true");
        this.addService();
      }
    }, 10);
  }

  changeGst(flipCgst: boolean) {
    if (flipCgst) {
      if (this.sales.isCgst) {
        this.sales.isIgst = false;
      } else {
        this.sales.isIgst = true;
      }
    } else {
      if (this.sales.isIgst) {
        this.sales.isCgst = false;
      } else {
        this.sales.isCgst = true;
      }
    }
  }

