  saveQuotation() {
    this.sales.status = "DRAFTSAVE";
    this.salesData();
    console.log(this.sales.items);
    if (this.valid()) {
      this.sales.isCredit = false;
      this.restService.saveQuotations(this.sales).subscribe(result => {
        console.log(result);
        if (result) {
          this.appService.showMessage("Quotation generated successfully  ");
          this.appService.navigate("admin/invoice-search", []);
        } else {
          this.appService.showMessage("Error while generating the quotation");
        }
      });
    }
  }

