  invoiceBill: any;
  billing() {
    this.sales.status = "BILLING";
    this.salesData();
    Storage.setLocalItem("selectedItem", this.selectedItem);
    if (this.valid()) {
      this.restService.saveInvoice(this.sales).subscribe(result => {
        if (result) {
          this.invoiceBill = result;
          setTimeout(() => {
            this.open();
          }, 1000);
        }
      });
    }
  }
  open() {
    // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
    Storage.setLocalItem("selectedItem", this.invoiceBill);
    let companyInfo = Storage.getSessionUser();
    let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
    if (parseInt(pageLayoutId) == 1) {
      const dialogRef = this.dialog.open(DialogContentExampleDialogInvoices, {
        data: null
      });
    }
  }

  update() {
    this.sales.status = "BILLING";
    this.salesData();
    console.log(this.sales);
    if (this.valid()) {
      this.restService.updateInvoice(this.sales).subscribe(res => {
        if (res) {
          this.appService.showMessage("Bill update successfully : " + res);
          this.appService.navigate("admin/invoice-search", []);
        }
      });
    }
  }

  clear() {
    this.sales = Object.assign({}, new Sales());
    this.sales.companyId = {};
    this.sales.companyId.id = this.appService.getBranch().id;
    this.sales.companyId.name = this.appService.getBranch().name;
    this.sales.companyId = this.appService.getBranch().id;
    this.sales.customer = new Customer();
    this.isPrintEnable = false;
    this.myForm = InvoiceFormGroup.init();
    InvoiceFormGroup.edit(this.myForm);
    InvoiceFormGroup.address(this.myForm, false);
    this.showAddress = false;
    this.sales.isProduct = true;
    this.sales.isService = false;
    setTimeout(() => {
      this.sales.isProduct = true;
      this.sales.isService = true;
      this.changeProductService(true);
    }, 100);
    //this.myForm.reset();
  }
  addItem() {
    this.addProduct();
  }
  onAddressChange($event) {
    if ($event.checked) {
      InvoiceFormGroup.address(this.myForm, false);
      this.showAddress = false;
    } else {
      InvoiceFormGroup.address(this.myForm, true);
      this.showAddress = true;
    }
  }

  isCreditChange($event) {
    if ($event.checked) {
      this.amount = false;

      // this.sales.isCreditHide = false;
    } else {
      this.amount = true;

      // this.sales.isCreditHide = false;
    }
  }

  isQuotatioChange($event) {
    if ($event.checked) {
      this.amount = false;
      this.isBilling = false;
      this.isCreditHide = false;
      this.isSave = false;
      this.isQuotationSave = true;
    } else {
      this.amount = true;
      this.isBilling = true;
      this.isCreditHide = true;
      this.isSave = true;
      this.isQuotationSave = false;
    }
  }

  termsLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.termsList = results["Terms"];
      });
  }
  printPriview() {
    this.restService.invoiceOpen(this.sales.id);
  }

  retriveServices() {
    this.restService.retriveServices().subscribe(result => {
      if (result) {
        this.services = result;
      } else {
        this.services = [];
      }
    });
  }

  retreiveInvoice(id: string) {
    this.restService.getInvoice(id).subscribe(results => {
      if (results) {
        this.sales = results;
        this.customerMobile =
          this.sales.customer.displayName + " " + this.sales.customer.mobile;
      } else {
        console.log("no results");
      }
    });
  }
