import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Sales } from "../../../../entities/sales.entity";
import { RestService } from "app/common/rest.service";
import { AppService } from "app/shared/service/app.service";
import { Address } from "app/entities/address.entity";
import { CustomerPayment } from "app/entities/customerpayment.entity";
import { CustomerTax } from "app/entities/customertax.entity";
import { Storage } from "../../../../shared/utils/storage";

@Component({
  selector: "app-products-service-toggle",
  templateUrl: "./products-service-toggle.component.html",
  styleUrls: ["./products-service-toggle.component.scss"]
})
export class ProductsServiceToggleComponent implements OnInit {
  @Input() myForm: any;
  @Input() sales: Sales;
  @Input() showAddress: boolean = false;
  @Output() outputProductToggle: EventEmitter<any> = new EventEmitter<any>();
  @Output() outputServiceToggle: EventEmitter<any> = new EventEmitter<any>();
  @Output() addProductEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() addServiceEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() changeGstEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() clearEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() addItemEmitter: EventEmitter<any> = new EventEmitter<any>();

  clear() {
    this.clearEmitter.emit();
  }
  add() {
    this.addItemEmitter.emit();
  }

  constructor(
    private restService: RestService,
    private appService: AppService
  ) {
    // this.sales.isProduct = false;
  }

  ngOnInit() {
    this.sales.isProduct = true;
    this.sales.isService = false;
    //   this.changeProduct(true);
    // this.changeService(true);
  }

  changeProduct(item: any) {
    this.outputProductToggle.emit(item);
    this.sales;
  }
  changeService(item: any) {
    this.outputServiceToggle.emit(item);
    this.sales;
  }

  addProduct(item: any) {
    console.log("toggle add product");
    this.outputProductToggle.emit(item);
  }
  addService(item: any) {
    this.outputServiceToggle.emit(item);
  }

  save() {
    this.sales.status = "DRAFTSAVE";
    this.salesData();
    console.log(this.sales.items);
    if (this.valid()) {
      this.restService.saveInvoice(this.sales).subscribe(result => {
        if (result) {
          this.appService.showMessage(
            "Invoice generated successfully : " + result.invoiceNumber
          );
          this.appService.navigate("admin/invoice-search", []);
        }
      });
    }
  }
  valid() {
    let returnVal = true;
    if (
      returnVal &&
      (!this.sales.customer.mobile ||
        this.sales.customer.mobile == "0" ||
        !this.sales.customer.firstName)
    ) {
      returnVal = false;
      this.appService.showMessage("please select customer name and mobile");
    }
    return returnVal;
  }
  billing() {
    this.sales.status = "BILLING";
    this.salesData();
    console.log(this.sales);
    if (this.valid()) {
      this.restService.saveInvoice(this.sales).subscribe(result => {
        if (result) {
          this.appService.showMessage(
            "Bill save successfully : " + result.invoiceNumber
          );
          this.appService.navigate("admin/invoice-search", []);
        }
      });
    }
  }
  salesData() {
    let productItems = [];
    let serviceItems = [];

    if (this.sales.isProduct) {
      productItems = this.sales.productItems.filter(item => {
        if (item.stockItemId.id != null) {
          item.service = null;
          return item;
        }
      });
    }
    if (this.sales.isService) {
      serviceItems = this.sales.serviceItems.filter(item => {
        if (item.service) {
          if (item.service.id != null) {
            item.stockItemId.id = null;
            return item;
          }
        }
      });
    }
    this.sales.items = [...productItems, ...serviceItems];

    if (this.sales.items == null || this.sales.items.length == 0) {
      this.appService.showMessage(
        "Please select atleast one product or service."
      );
    }
    if (this.sales.customer.id == null) {
      if (!this.sales.customer.address) {
        this.sales.customer.address = new Address();
      }
      this.sales.customer.address.lane = this.sales.customer.inlineAddress;
      if (this.sales.isBillingAddress) {
        this.sales.customer.shippingAddress = this.sales.customer.address;
      } else {
        this.sales.customer.shippingAddress = this.sales.address;
      }
      this.sales.customer.customerPayment = new CustomerPayment();
      this.sales.customer.customerTax = new CustomerTax();
    }
    this.sales.userId = 1; // this.appService.getSessionUser().id;
    console.log(this.appService.getSessionUser());
  }
}



