import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreateGoodsReturnedBySupplierComponent } from "../../modules/goods-returned/containers/create-goods-returned-by-supplier/create-goods-returned-by-supplier.component";
import { CreateGoodsReturnedByCustomerComponent } from "../goods-returned/containers/create-goods-returned-by-customer/create-goods-returned-by-customer.component";

const routes: Routes = [
  {
    path: "goodsreturnedbysupplier",
    component: CreateGoodsReturnedBySupplierComponent
  },
  {
    path: "goodsreturnedbycustomer",
    component: CreateGoodsReturnedByCustomerComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GoodsReturnedRoutingModule {}



