import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { CreateGoodsReturnedBySupplierComponent } from "../../modules/goods-returned/containers/create-goods-returned-by-supplier/create-goods-returned-by-supplier.component";
import { CreateGoodsReturnedByCustomerComponent } from "../goods-returned/containers/create-goods-returned-by-customer/create-goods-returned-by-customer.component";
import { GoodsReturnedRoutingModule } from "./goods-returned-routing.module";
import { SharedModule } from "../../shared/shared.module";
import { CustomerGoodsReturnComponent } from "./components/search-customer-goods-return/customer-goods-return.component";
import { SuppliersGoodsReturnComponent } from "./components/search-suppliers-goods-return/suppliers-goods-return.component";
import { CustomerProductTableComponent } from "./components/customer-product-table/customer-product-table.component";
import { SupplierProductTableComponent } from "./components/supplier-product-table/supplier-product-table.component";
import { ProductsServiceToggleComponent } from "./components/products-service-toggle/products-service-toggle.component";

@NgModule({
  imports: [CommonModule, GoodsReturnedRoutingModule, SharedModule],
  declarations: [
    CreateGoodsReturnedBySupplierComponent,
    CreateGoodsReturnedByCustomerComponent,
    CustomerGoodsReturnComponent,
    SuppliersGoodsReturnComponent,
    CustomerProductTableComponent,
    SupplierProductTableComponent,
    ProductsServiceToggleComponent
  ]
})
export class GoodsReturnedModule {}



